import React from 'react';
import { Box, Button } from '@chakra-ui/react';
import { RepeatIcon } from '@chakra-ui/icons';

const CircularButtonWithIcon = () => {
  return (
    <Box>
      <Button
        size={"sm"}
        variant="ghost"
        borderRadius="50%" //   
        width="40px" //    ,    
        height="40px"
        display="flex"
        justifyContent="center"
        alignItems="center"
        colorScheme="black" //  
      >
        <RepeatIcon  />
      </Button>
    </Box>
  );
};

export default CircularButtonWithIcon;